'use client';

import Header from '../../../components/header/page';
import Footer from '../../../components/footer/page';
import { useEffect, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Link from 'next/link';

const MensNewArrivalsPage: React.FC = () => {
  const sectionRef = useRef<HTMLDivElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    const ctx = gsap.context(() => {
      const container = containerRef.current!;
      const section = sectionRef.current!;
      
      const scrollLength = container.scrollWidth - window.innerWidth;

      gsap.set(container, { width: container.scrollWidth });
      gsap.set(section, { height: 'auto' });

      gsap.to(container, {
        x: () => -scrollLength,
        ease: 'none',
        scrollTrigger: {
          trigger: section,
          start: 'top top',
          end: () => `+=${scrollLength}`,
          scrub: true,
          pin: container,
          anticipatePin: 1,
          invalidateOnRefresh: true,
        },
      });
    }, sectionRef);

    return () => ctx.revert();
  }, []);

  const featuredSections = [
    {
      image: 'https://www.madewell.com/brand_creative/mw-jpg/2025/m-2025-jun-live06-10-hp-img1.jpg?wid=1500',
      text: 'New Arrival 1',
      link: 'https://www.everlane.com/collections/mens-vacation-shop',
    },
    {
      image: 'https://www.madewell.com/brand_creative/mw-jpg/2025/m-2025-jun-live06-10-hp-img2.jpg?wid=1500',
      text: 'New Arrival 2',
      link: 'https://www.everlane.com/collections/mens-vacation-shop',
    },
  ];

  const categoryCards = [
    {
      title: 'Shirting',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/0aa29bb5_594e/mens-resort-shirt-butterlite-dark-mahogany',
      bgColor: '#f0f0f0',
      link: '/shirting',
    },
    {
      title: 'Knit Tops',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/94a9fe20_87ec/mens-prem-wt-rlxd-crew-glacier-gray',
      bgColor: '#f0f0f0',
      link: '/knit-tops',
    },
    {
      title: 'Sweaters',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/505ff14f_2716/mens-cotton-linen-pullover-eucalyptus',
      bgColor: '#f0f0f0',
      link: '/sweaters',
    },
    {
      title: 'Bottoms',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/a6a8be1a_6af2/mens-linen-easy-short2-navy',
      bgColor: '#f0f0f0',
      link: '/bottoms',
    },
    {
      title: 'Bags',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/815cbd53_f5dc/mens-renew-backpack-dark-forest',
      bgColor: '#f0f0f0',
      link: '/bags',
    },
    {
      title: 'Denim',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/c02bbdba_ce84/mens-90s-loose-straight-jean-mdm-blue',
      bgColor: '#f0f0f0',
      link: '/denim',
    },
    {
      title: 'Jackets',
      image: 'https://www.madewell.com/images/NV071_GR7316_ld?wid=1200&hei=1525&fmt=jpeg&fit=crop&qlt=75',
      bgColor: '#f0f0f0',
      link: '/jackets',
    },
    {
      title: 'Shoes',
      image: 'https://www.madewell.com/images/NR616_EE7426_ld?wid=1200&hei=1525&fmt=jpeg&fit=crop&qlt=75',
      bgColor: '#f0f0f0',
      link: '/shoes',
    },
  ];

  const sizes = ['XS', 'S', 'M', 'L', 'XL', 'XXL', 'XXXL'];
  const colors = [
    { name: 'White', count: 5 },
    { name: 'Black', count: 8 },
    { name: 'Denim', count: 3 },
    { name: 'Blue', count: 11 },
    { name: 'Grey', count: 1 },
    { name: 'Pink', count: 2 },
    { name: 'Purple', count: 2 },
    { name: 'Brown', count: 9 },
    { name: 'Green', count: 10 },
    { name: 'Multi', count: 2 },
    { name: 'Natural', count: 11 },
    { name: 'Gold', count: 1 },
  ];

  const newArrivals = [
    {
      id: '1',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/0aa29bb5_594e/mens-resort-shirt-butterlite-dark-mahogany',
      text: 'The Resort Shirt in Butterlite',
      price: '₹8800',
      colors: ['#4A2C2A', '#8B4513', '#2F4F4F'],
      tags: ['Responsible Forestry'],
    },
    {
      id: '2',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/94a9fe20_87ec/mens-prem-wt-rlxd-crew-glacier-gray',
      text: 'The Rec Performance Tank',
      price: '₹3410',
      colors: ['#F5F5F5', '#A9A9A9', '#D3D3D3', '#FFFFFF'],
      tags: ['Organic Cotton', 'Cleaner Chemistry', 'Ever-Better Factory'],
    },
    {
      id: '3',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/c02bbdba_ce84/mens-90s-loose-straight-jean-mdm-blue',
      text: 'The Pique Polo',
      price: '₹8800',
      colors: ['#000000', '#333333', '#666666', '#999999', '#CCCCCC', '#FFFFFF', '#8A2BE2', '#DAA520'],
      tags: ['Cleaner Cotton', 'Ever-Better Factory'],
    },
    {
      id: '4',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/505ff14f_2716/mens-cotton-linen-pullover-eucalyptus',
      text: 'The Linen Jacket',
      price: '₹9500',
      colors: ['#228B22', '#6B8E23', '#9ACD32'],
      tags: ['Organic Linen', 'Sustainable Practices'],
    },
    {
      id: '5',
      image: 'https://media.everlane.com/images/c_fill,w_828,ar_4:5,q_auto:best:sensitive,dpr_2.0,f_auto/i/a6a8be1a_6af2/mens-linen-easy-short2-navy',
      text: 'The Slim Fit Jeans',
      price: '₹6200',
      colors: ['#000080', '#1E90FF', '#4682B4'],
      tags: ['Eco-Friendly', 'Responsible Sourcing'],
    },
    // ... (rest of the newArrivals array remains the same, with unique IDs)
  ];

  return (
    <div className="min-h-screen bg-stone-50 text-stone-900 font-serif">
      <Header />
      <main>
        {/* New Arrivals Section */}
        <section className="relative w-full h-[95vh] overflow-hidden">
          <div className="flex w-full h-full">
            {featuredSections.map((section, index) => (
              <div key={index} className="w-1/2 h-full relative overflow-hidden">
                <img src={section.image} alt={section.text} className="w-full h-full object-cover" />
              </div>
            ))}
          </div>
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-center text-white z-10 w-full">
            <div className="text-6xl font-bold">NEW ARRIVALS</div>
            <div className="text-2xl mt-2.5">Our thoughtful images to the latest collection.</div>
            <a href={featuredSections[0].link} className="text-xl underline mt-2.5 inline-block">
              Shop new arrivals
            </a>
          </div>
        </section>

        {/* Explore Categories Section */}
        <section ref={sectionRef} className="relative overflow-hidden bg-[#f4f7f8]">
          <h2 className="text-3xl font-bold text-center mb-12">Explore Categories</h2>
          <div ref={containerRef} className="flex gap-6 px-10">
            {categoryCards.map((card, index) => (
              <a
                key={index}
                href={card.link}
                className="w-[calc(100vw/3.05)] h-full rounded-2xl flex-shrink-0 p-6 transition-transform shadow-md"
                style={{ backgroundColor: card.bgColor }}
              >
                <h3 className="text-2xl font-semibold mb-4">{card.title}</h3>
                <div
                  className="w-full h-[80%] rounded-xl overflow-hidden"
                  style={{ backgroundColor: card.bgColor }}
                >
                  <img
                    src={card.image}
                    alt={card.title}
                    className="w-full h-full object-contain"
                  />
                </div>
              </a>
            ))}
          </div>
          {/* Filter Section */}
          <div className="w-full py-6 px-4 bg-white">
            <div className="flex items-center space-x-4">
              <div className="relative">
                <select className="appearance-none bg-transparent border-none text-stone-900 text-lg font-medium pr-8 focus:outline-none">
                  <option>Sort by: Relevance</option>
                  <option>Best Sellers</option>
                  <option>Price: High - Low</option>
                  <option>Price: Low - High</option>
                  <option>New</option>
                  <option>Top Rated</option>
                </select>
                <span className="absolute right-2 top-1/2 transform -translate-y-1/2 pointer-events-none">↑↓</span>
              </div>
              <div className="relative">
                <select className="appearance-none bg-transparent border-none text-stone-900 text-lg font-medium pr-8 focus:outline-none">
                  <option>Size</option>
                  {sizes.map((size) => (
                    <option key={size}>{size}</option>
                  ))}
                </select>
                <span className="absolute right-2 top-1/2 transform -translate-y-1/2 pointer-events-none">▼</span>
              </div>
              <div className="relative">
                <select className="appearance-none bg-transparent border-none text-stone-900 text-lg font-medium pr-8 focus:outline-none">
                  <option>Color</option>
                  {colors.map((color) => (
                    <option key={color.name}>{color.name}</option>
                  ))}
                </select>
                <span className="absolute right-2 top-1/2 transform -translate-y-1/2 pointer-events-none">▼</span>
              </div>
              <div className="relative">
                <select className="appearance-none bg-transparent border-none text-stone-900 text-lg font-medium pr-8 focus:outline-none">
                  <option>Category</option>
                  {categoryCards.map((card) => (
                    <option key={card.title}>{card.title}</option>
                  ))}
                </select>
                <span className="absolute right-2 top-1/2 transform -translate-y-1/2 pointer-events-none">▼</span>
              </div>
            </div>
          </div>
        </section>

        {/* New Arrivals Products Section */}
        <section className="py-10 px-4 bg-white">
          <h2 className="text-3xl font-bold text-center mb-8">New Arrivals</h2>
          <div className="grid grid-cols-3 gap-6">
            {newArrivals.map((item, index) => (
              <Link key={index} href={`/overview?page=overview&id=${item.id}`} className="border rounded-lg p-4 shadow-md block">
                <div className="relative">
                  <img src={item.image} alt={item.text} className="w-full h-80 object-cover" />
                  {(index === 0 || index === 1) && (
                    <span className="absolute top-2 left-2 bg-white text-black px-2 py-1 text-sm font-semibold">
                      NEW
                    </span>
                  )}
                </div>
                <h2 className="text-lg font-semibold mt-4">{item.text}</h2>
                <p className="text-gray-600 mt-2">{item.price}</p>
                <div className="flex space-x-2 mt-2">
                  {item.colors.map((color, colorIndex) => (
                    <div
                      key={colorIndex}
                      className="w-4 h-4 rounded-full border"
                      style={{ backgroundColor: color }}
                    ></div>
                  ))}
                </div>
                <p className="text-gray-500 text-sm mt-2">{item.tags.join(', ')}</p>
              </Link>
            ))}
          </div>
        </section>
      </main>
      <Footer />
    </div>
  );
};

export default MensNewArrivalsPage;